.social-contact{
    display: flex;
    justify-content: center;
    align-items: center;
}
.social-icon-div{
    padding: 6px;
    cursor: pointer;
    margin: 6px;
    transition: all 0.3s ease-in-out;
}
.social-icon{
    height: 40px;
}
.social-icon-div:hover{
    transform: scale(1.2);
}
@media only screen and (max-width: 720px) {
    .social-icon{
        height: 35px;
    }
}