.project-card{
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;
    margin: 16px 0px;
    padding: 8px 0px;
}
.project-photo{
    width: 50%;
    box-shadow: 2px 2px 20px black;
    transition: all 0.3s ease-in-out;
}
.project-photo:hover{
    transform: scale(1.1);
}
.project-info{
    margin-right: 24px;
}
.project-title{
    font-size: 20px;
    font-weight: 600;
}
.project-tags{
    display: flex;
    max-width: 60%;
    flex-wrap: wrap;
}
.tag{
    padding: 4px 8px;
    background-color: aquamarine;
    border-radius: 4px;
    margin-right: 6px;
    margin-bottom: 4px;
    transition: all 0.3s ease-in-out;
}
.tag:hover{
    margin-right: 10px;
    margin-left: 10px;
    transform: scale(1.2);
}
.project-links a{
    text-decoration: none;
    color: black;
}
.project-links{
    margin-top: 6px;
    display: flex;
}
.link-button{
    display: flex;
    justify-content: center;
    align-items: center;
    width: max-content;
    background-color: aquamarine;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 16px;
    margin-right: 6px;
}
.link-button i{
    display: flex;
    align-items: center;
    margin-right: 4px;
}
@media only screen and (max-width: 720px) {
    .project-photo{
        width: 90%;
        margin-top: 20px;
    }
    .project-photo:hover{
        width: 95%;
    }
    .project-card{
        text-align: center;
        flex-direction: column-reverse;
    }
    .project-info{
        margin-left: 0px;
        margin-top: 12px;
    }
    .project-tags{
        justify-content: center;
        max-width: 100%;
    }
    .project-links{
        justify-content: center;
    }
}

