.skill-card{
    width: max-content;
    padding: 8px 16px;
    height: 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 6px;
}
.skill-icon{
    font-size: 80px;
    transition: all 0.3s ease-in-out;
}
.skill-name{
    font-size: 18px;
}
@media only screen and (max-width: 720px) {
    .skill-card{
        width: max-content;
        height: 100px;
    }
    .skill-icon{
        font-size: 60px;
    }
}
.skill-icon:hover{
    z-index: 1;
    transform: scale(1.3);
    margin-bottom: -5px;
}