.c-container{
    position: relative;
    margin-bottom: 50px;
}
.c-row{
    display: table;
    width: 100%;
    text-align: center;
}
.c-column{
    display: table-cell;
    max-width: 720px;
}
.c-section-title{
    font-size: 24px;
    font-weight: 600;
}
.c-img{
    width: 70%;
    transition: all 0.3s ease-in-out;
}
@media only screen and (max-width: 720px) {
    .c-row{
        display: table-cell;
    }
    .c-column{
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 20px;
    }
}
.c-img:hover{
    transform: scale(1.3);
}