.header{
    display: flex;
    justify-content: space-between;
    flex: 1;
    padding: 12px;
    position: relative;
    width: 90%;
    align-items: center;
    border-radius: 5px;
    background-color: white;
    z-index: 9;
    margin: auto;
    top: 5px;
}
.menu{
    display: flex;
    flex: 1;
    justify-content: flex-end;
}
.menu-icon{
    font-size: 24px;
}
.mobile-menu{
    display: none;
}
.web-menu{
    display: flex;
}
.logo{
    display: flex;
    font-size: 36px;
    color: blueviolet;
    font-weight: 800;
}
@media only screen and (max-width: 720px){
    .header{
        margin: auto;
    }
    .mobile{
        left: 50%;
        transform: translate(-50%, 0);
        border-radius: 5px;
    }
    .mobile-menu{
        display: flex;
    }
    .web-menu{
        display: none;
    }
    .logo{
        font-size: 24px;
    }
}