.about{
    margin-top: 50px;
    padding-bottom: 48px;
}
.about-top{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.img{
    height: 400px;
}
.about-info{
    font-size: 25px;
}
.info-name{
    color: blue;
    font-size: 25px;
    
}
.typing{
    width: 19.5ch;
    animation: typing 3s steps(19), blink .5s step-end infinite alternate;
    white-space: nowrap;
    overflow: hidden;
    border-right: 3px solid;
    color: blue;
    font-size: 25px;
}
@keyframes typing {
    from {
      width: 0
    }
}
@keyframes blink {
    50% {
      border-color: transparent
    }
}
@media only screen and (max-width: 720px) {
    .about-top{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .about-info{
        font-size: 20px;

    }
    .info-name{
        font-size: 26px;
    }
    .img{
        height: 300px;
    }
}